* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
button {
  font-family: "futura-pt";
  font-size: 16px;
}

html {
  height: 100%;
}

header {
  background: #ff5a66;
  color: #fff;
  text-align: center;
  flex-grow: 0;
}

#root {
  min-height: 100vh;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  background: #ffffff;
  flex-grow: 1;
}

form {
  max-width: 320px;
  margin: 0 auto;
}

form > div {
  width: 100%;
  margin-bottom: 1em;
}
form > div > label {
  display: block;
  margin-bottom: 0.3em;
}
form > div > input {
  display: block;
  width: 100%;
  font-size: 16px;
  padding: 0.4em;
  border-radius: 6px;
  border: 1px solid #2a2e35;
}

.home-nav-bar {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  top: 0;
  width: 100%;
  background-color: white;
  z-index: 1;
  height: 36px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.18);
}

.padding-left-8 {
  padding-left: 8px;
}

.padding-left-16 {
  padding-left: 16px;
}

.margin-top-2 {
  margin-top: 2px;
}

.margin-top-4 {
  margin-top: 4px;
}

.margin-top-8 {
  margin-top: 8px;
}

.margin-top-10 {
  margin-top: 10px;
}

.margin-top-12 {
  margin-top: 12px;
}

.margin-top-16 {
  margin-top: 16px;
}

.margin-top-32 {
  margin-top: 32px;
}

.margin-top-64 {
  margin-top: 64px;
}

input {
  font-family: "futura";
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input:focus {
  outline: none;
}

.center {
  text-align: center;
}

.button {
  height: 56px;
  width: 320px;
  color: #fff;
  border-radius: 100px;
  font-size: 16px;
  font-weight: 900;
  letter-spacing: 0.5px;
  max-width: 90%;
  background-color: #363c44;
  font-family: "futura";
  cursor: pointer;
  border: none;
}

.button-delete {
  height: 56px;
  width: 300px;
  color: #fff;
  border-radius: 100px;
  font-size: 16px;
  font-weight: 900;
  letter-spacing: 0.5px;
  max-width: 90%;
  background-color: #ff5a66;
  font-family: "futura";
  cursor: pointer;
  border: none;
}

.gray-button {
  height: 56px;
  width: 320px;
  color: #fff;
  border-radius: 100px;
  font-size: 16px;
  font-weight: 900;
  letter-spacing: 0.5px;
  background-color: #363c44;
  font-family: "futura";
  cursor: pointer;
  border: none;
}

.futura-12-300 {
  font-family: "futura";
  font-size: 12px;
  font-weight: 300;
  letter-spacing: 1px;
  line-height: 16px;
}

.futura-14-300 {
  font-family: "futura";
  font-size: 14px;
  font-weight: 300;
  line-height: 14px;
}

.futura-16-300 {
  font-family: "futura";
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 0.15px;
}

.futura-16-900 {
  font-family: "futura";
  font-size: 16px;
  font-weight: 900;
  letter-spacing: 0.15px;
}

.futura-14-600 {
  font-family: "futura";
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0.15px;
}

.futura-14-900 {
  font-family: "futura";
  font-size: 14px;
  font-weight: 900;
  letter-spacing: 0.15px;
}

.futura-20-300 {
  font-family: "futura";
  font-size: 20px;
  font-weight: 300;
}

.futura-20-900 {
  font-family: "futura";
  font-size: 20px;
  font-weight: 900;
}

.futura-22-900 {
  font-family: "futura";
  font-size: 22px;
  font-weight: 900;
}

.futura-24-900 {
  font-family: "futura";
  font-size: 24px;
  font-weight: 900;
}

.video-screen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  padding: 16px 16px 64px 16px;
  background: #fff;
  justify-content: center;
  align-items: center;
}

.video-preview-screen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  background: #fff;
  justify-content: center;
  align-items: center;
  overflow: overlay;
}

@media (min-width: 960px) {
  .form {
    width: 40vw;
    justify-content: center;
    align-items: center;
  }
}

.button:disabled,
button[disabled] {
  background-color: #999999;
  color: white;
}

.exit-button {
  cursor: pointer;
  text-align: right;
  padding: 16px;
}

.bottom-button {
  text-align: center;
  padding-bottom: 16px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}

.bottom-button-video {
  background-color: white;
  text-align: center;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-1 {
  flex: 1;
}

.sets-pic {
  max-width: 64px;
  background: rgb(255, 90, 102);
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  color: white;
  text-align: center;
  font-weight: 900;
  font-size: 24px;
}

.add-exercises {
  max-width: 64px;
  background: #c5c5c5;
  width: 64px;
  height: 44px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.sets-title {
  background-color: #f1f1f1;
  padding-left: 16px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.sets-subtitle {
  height: 20px;
  color: #6f6f6f;
  font-family: "futura";
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.29px;
  line-height: 20px;
}

.exercises-container {
  position: relative;
  background: #f1f1f1;
  border-radius: 5px;
  padding-right: 8px;
  cursor: pointer;
}

.topright {
  position: absolute;
  right: 0;
}

.edit-button {
  height: 42px;
  width: 42px;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  background: #28a745;
}

.delete-button {
  height: 42px;
  width: 42px;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  background: #dc3545;
}

.users-container {
  margin-top: 12px;
  background: #f1f1f1;
  border-radius: 5px;
  padding: 8px;
}

.exercise-container {
  cursor: pointer;
  background: #f1f1f1;
  border-radius: 5px;
}

.existing-program-container {
  background: #f1f1f1;
  border-radius: 5px;
}

@media (min-width: 960px) {
  .background {
    background-image: url("../HomePage/assets/icons/background.svg");
    background-size: cover;
    height: 100vh;
    background-repeat: no-repeat;
  }
}
